import { Field, InputType } from "@nestjs/graphql";
import { IsNotEmpty, IsUUID } from "class-validator";
import { PaymentPlan } from "../../../../orm-config";
import { RelationExists } from "../../../../validation";


@InputType()
export class PaymentPlanInput {

  @Field(() => String)
  @IsNotEmpty()
  @RelationExists(PaymentPlan)
  @IsUUID(4)
  id: string;

}
